<template>
  <div class="v-calendar">
    <v-calendar :attributes='attributes'
                @update:fromPage='pageChanged'
                @update:toPage='pageChanged'
                is-expanded>
    </v-calendar>
  </div>
</template>

<script>
  import Vue from 'vue'
  import VCalendar from 'v-calendar'
  import client from '../clients/eventsClient';
  import eventFactory from '../utils/eventFactory';
  import cache from '../utils/cache';
  import 'v-calendar/lib/v-calendar.min.css';

  const todos = [
    {
      description: 'It\'s friday',
      isComplete: false,
      dates: {weekdays: 6}, // Every Friday
      backgroundColor: '#ff8080',       // Red
    },
    {
      popover: {
        label: '14.-18.1.2019',
        slot: 'event',
      },
      isComplete: false,
      highlight: {
        backgroundColor: '#ff8080',
        borderColor: '##ff8080',
        borderWidth: '2px',
        borderStyle: 'solid',
      },
      event: {
        text: 'event'
      },
      dates: [
        {
          start: new Date(2019, 0, 14),
          end: new Date(2019, 0, 18)
        }
      ],
    },
  ];

  Vue.use(VCalendar, {
    formats: {
      title: 'MMMM YYYY',
      weekdays: 'W',
      navMonths: 'MMM',
      input: ['L', 'YYYY-MM-DD', 'YYYY/MM/DD'],
      dayPopover: 'L',
    }
  });

  export default {
    data() {
      return {
        rawEvents: []
      };
    },
    computed: {
      events() {
        return this.rawEvents
          .map((event) => {
            return eventFactory.calendarEvent(event);
          })
      },
      attributes() {
        return [
          // Today
          {
            contentStyle: {
              fontWeight: '800',
              color: '#66b3cc',
            },
            dates: new Date(),
          },
          // Attributes for todos
          ...this.events
        ]
      }
    },
    mounted() {
      this.$nextTick(function () {
        const now = new Date();
        this.fetchEvents(now.getFullYear(), now.getMonth() + 1)
      })
    },
    methods: {
      pageChanged(page) {
        this.fetchEvents(page.year, page.month);
      },
      fetchEvents(year, month) {
        cache(`${year}.${month}`, () => {
          client.get(year, month)
            .then(events => {
              events.forEach((event) => {
                this.rawEvents.push(event)
              });
            });
            return true;
        });
      }
    }
  };
</script>
