export default {
  singleEvent(event, date) {
    const dates = date;
    return {
      popover: {
        label: event.title,
        slot: 'event',
      },
      highlight: {
        backgroundColor: '#ff8080',
        borderColor: '##ff8080',
        borderWidth: '2px',
        borderStyle: 'solid',
      },
      event,
      dates,
    }
  },
  longEvent(event, start, end) {
    const dates = [
      {
        start,
        end
      }
    ];
    return {
      popover: {
        label: event.title,
        slot: 'event',
      },
      highlight: {
        backgroundColor: '#ff165c',
        borderColor: '##ff8080',
        borderWidth: '2px',
        borderStyle: 'solid',
      },
      event,
      dates,
    }
  },
  calendarEvent(event) {
    const startDate = new Date(event.start_time);
    const endDate = event.end_time ? new Date(event.end_time) : null;

    return endDate
      ? this.longEvent(event, startDate, endDate)
      : this.singleEvent(event, startDate)
  }
}
